import React, { FC } from "react";
import ReactDOM from "react-dom";
import App from "./App";
import "./index.css";

function renderApp(NextApp: FC) {
  ReactDOM.render(
    <React.StrictMode>
      <NextApp />
    </React.StrictMode>,
    document.getElementById("root")
  );
}

if (process.env.NODE_ENV === "development") {
  if (module.hot) {
    module.hot.accept("./App", () => {
      // install --save-dev @types/webpack-env
      const NextApp: FC = require("./App").default;

      renderApp(NextApp);
    });
  }
}

renderApp(App);
